import React, { useEffect, useState } from "react";
import {
    CButton,
    CCol,
    CModal,
    CModalBody,
    CRow
} from "@coreui/react";
import "Component/Modal/PopUp/index.scss";
import { dateFull } from 'services'

const updateSuccessHandler = ({ user, setUser }) => {
    if (user?.code === 200) setUser()
    else setUser({ ...user, submitted: false })
}

const Saved = ({ user, setUser, status }) => {
    const saved = {
        success: {
            headerIcon: 'em em-tada',
            textColor: 'green',
            button: 'bg-green text-white',
            date: dateFull({ date: new Date }),
            title: 'Success',
            message1: 'User Edited Successfully',
            message2: '',
            message3: '',
            buttonText: 'Got it!',
            userName: user?.email ? user?.email : ''
        },
        failed: {
            headerIcon: 'em em-anguished',
            textColor: 'red',
            button: 'bg-red text-white',
            date: '',
            title: 'Failed',
            message1: '',
            message2: user?.message,
            message3: 'Would you like to retry?',
            buttonText: 'Check User Data',
            userName: ''
        },
        error: {
            headerIcon: 'em em-anguished',
            textColor: 'red',
            button: 'bg-red text-white',
            date: '',
            title: 'Failed',
            message1: '',
            message2: 'Server error. Please Contact the admin',
            message3: 'Failed to update User Data.',
            buttonText: 'Try Again',
            userName: ''
        }
    }
    let isError = false
    if (user?.code === 200) isError = false
    else isError = true
    window.onkeydown = e => e.keyCode === 27 ? updateSuccessHandler({ user, setUser }) : null

    let isSuccess = saved.success
    if (user?.code === 200) isSuccess = saved.success
    else isSuccess = saved.failed
    return (
        <CRow className={user?.submitted ? null : 'd-none'}>
            <CCol>
                <CModal
                    className={``}
                    show={user?.submitted}

                    onClose={() => null}
                    size="lg"
                    style={{ minHeight: "280px" }}
                    bodyOpenClassName="p-3"
                    closeOnBackdrop={false}
                >
                    <div className="d-flex flex-row-reverse">
                        <i onClick={() => updateSuccessHandler({ user, setUser })} className="pr-3 pt-3 iconU-modalClose" ></i>
                    </div>
                    <CModalBody className="px-5 pb-5 pt-3 d-flex justify-content-between flex-column">
                        <div>
                            <label className={`${isSuccess.textColor} LatoBold mr-2 h5 mb-3`}>{isSuccess.title}</label> <span><i className={`${isSuccess.headerIcon}`}></i></span>
                        </div>
                        <div className='text mb-3 h6'>{isSuccess.message1}</div>

                        {/* <div style={{ backgroundColor: '#F5F7FA', opacity: 1 }} className={`w-100 border rounded px-3 pt-3 pb-3 mb-3 ${success ? null : 'd-none'}`}>
                            <div className='mb-0'>
                                <div className={`mr-1 ${success ? null : 'd-none'}`}>
                                    <div style={{ wordWrap: 'break-word', opacity: 1, height: true ? false : 0 }} className={`mr-1`}>
                                        <div className={`h5 text mb-3 ${success ? null : 'd-none'}`}>{isSuccess.date}</div>
                                        <div style={{ fontSize: '1rem' }} className={`${success ? null : 'd-none'} font-green`}>{`By ${isSuccess.userName}`}</div>
                                    </div>
                                </div>
                            </div>
                        </div> */}
                        <div style={{ fontSize: '1rem' }} className={`${status?.code !== 200 ? null : 'd-none'} text mb-3 h6`}>{`${isSuccess.message3}`}</div>
                        <div style={{ fontSize: '1rem' }} className={`${status?.code !== 200 ? null : 'd-none'} text mb-3 h6`}>{`${isSuccess.message2}`}</div>
                        <div className='w-100 d-flex '>
                            <CButton onClick={() => updateSuccessHandler({ setUser, user })} className={`no-shadow px-5 mr-4 mb-3 ${isSuccess.button}`}>
                                {isSuccess.buttonText}
                            </CButton>
                        </div>
                    </CModalBody>
                </CModal>
            </CCol>
        </CRow >
    );
};

export default Saved;
