import React, { useEffect } from 'react'
import { CCol, CModal, CModalBody, CModalHeader, CRow, CSwitch, CButton, CTooltip } from "@coreui/react";
import { heightStyle } from './services'
import Img from 'assets/icons/sayur-box-logo.svg'
import AdminIcon from 'assets/icons/adminIcon.svg'
import Dropdown from 'Component/Dropdown'
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { submitUserList } from 'apiServices';

const onChangeUserData = ({ user, setUser, value, type }) => {
    const newUser = { ...user }
    let newModules = []
    let newCategories = []
    switch (type) {
        case 'userRole':
            if (value.value) newUser.role = value
            else break
            setUser(newUser)
            break
        case 'userModule':
            const userModules = user?.modules?.map(data => data.value) || []

            if (userModules.includes(value?.value)) {
                newModules = newUser.modules.filter(data => data.value !== value.value)
                newUser.modules = newModules
                newUser.listModuleAccess = newModules
            }
            else {
                newModules = [...newUser.modules, value]
                newUser.modules = newModules
                newUser.listModuleAccess = newModules
            }
            setUser(newUser)
            break
        case 'userCategory':
            const userCategories = user?.categories?.map(data => data.value) || []
            if (userCategories.includes(value?.value)) {
                newCategories = newUser.categories.filter(data => data.value !== value.value)
                newUser.categories = newCategories
                newUser.listCategoryUuid = newCategories
            }
            else {
                newCategories = [...newUser.categories, value]
                newUser.categories = newCategories
                newUser.listCategoryUuid = newCategories
            }
            setUser(newUser)
            break
    }
}

const closeUserModalHandler = ({ user, setUser }) => {
    let newUser = { ...user }
    newUser.show = false
    setUser()
}

const UMEdit = ({ setUser, user }) => {
    const inventoryModules = useSelector(state => state.inventoryModules)
    const thereAreRequest = useSelector(state => state.thereAreRequest)
    const categoryList = useSelector(state => state.categoryList)
    const userRoles = useSelector(state => state.userRoles)
    const [category, setCategory] = useState('')
    const submit = true

    const userName = user?.email
    const userArea = `${user?.role?.label} - JK01 - Sentul`
    const userModules = user?.modules?.map(data => data.value) || []
    const userCategories = user?.categories?.map(data => data.value) || []
    const userRole = userRoles?.filter(data => data.value === user?.role?.value)[0]

    let isAdmin = 0
    if (user?.role?.value === 'admin') isAdmin = 1
    else isAdmin = 0

    const width = document.documentElement.clientWidth
    let height = document.documentElement.clientHeight - 130
    const categoryHeight = width <= 500 ? '60%' : '75%'

    let showUser = false
    if (user?.show && !user?.submitted) showUser = true
    else showUser = false
    return (
        <CRow onKeyDown={e => e.keyCode === 27 ? closeUserModalHandler({ user, setUser }) : false} className={`${showUser ? null : 'd-none'} single-view user-management`}>
            <CCol>
                <CModal
                    closeOnBackdrop={false}
                    show={showUser}
                    onClose={() => closeUserModalHandler({ user, setUser })}
                    className='w-100'
                >
                    <CModalHeader className="px-3 pt-3  border-none m-0 d-flex align-items-center">
                        <div className='LatoBold softGrey'>User Management - Edit User</div>  <i className="iconU-modalClose LatoBold" onClick={() => closeUserModalHandler({ user, setUser })} />
                    </CModalHeader>
                    <CModalBody className="pl-3 pr-1 py-0 w-100 overflow-y border-top pt-3 pb-3" style={heightStyle(height + 15)}>
                        <div className=' d-flex align-items-center bg-softGreen rounded p-3 mb-3'>
                            <div className={`d-flex align-items-center w-100 cursor-pointer ${width <= 500 ? 'pl-2 pr-3' : 'pl-0 pr-0'}`}>
                                <img
                                    src={Img} alt='sku image'
                                    className='w-5 mr-3 border softGreen rounded' />
                                <div className='text-left'>
                                    <div className='mr-3 text LatoBold fontSizeTitle d-flex'>{userName}
                                        <CTooltip advancedOptions={{ appendToBody: true, html: true }} content={`<div style='text-align:left'>Only Admin able to give module and SKU category access to other users.</div>`} placement={width <= 500 ? 'left' : 'right'}>
                                            <div>
                                                <img style={{
                                                    opacity: isAdmin,
                                                    transition: 'opacity ease-in',
                                                    transitionProperty: 'all',
                                                    transitionDuration: '0.10s',
                                                }} className={`pl-1 ${userRole?.value === 'admin' ? '' : 'd-none'}`} alt='img' src={AdminIcon} />
                                            </div>
                                        </CTooltip>
                                    </div>
                                    <div className='text'>{userArea}</div>
                                </div>
                            </div>
                        </div>

                        <div style={{ backgroundColor: '#F9FAFC' }} className='mb-3 p-3 rounded  d-flex justify-content-between align-items-center'>
                            <div style={{ height: 'fit-content' }} className='LatoBold'>User Level Access</div>
                            <Dropdown onChangeDropdown={(value) => onChangeUserData({ user, setUser, value, type: 'userRole' })} show={true} width='w-50' placeholder='Select Role' style={{}} noMarginBottom={true} options={userRoles} selectedValue={userRole} title='User Roles' />
                        </div>

                        <div style={{ backgroundColor: '#F9FAFC' }} className='mb-3 p-3 rounded  h-25'>
                            <div style={{ height: 'fit-content' }} className='LatoBold pb-3'>User Module Access <span>{`( ${userModules?.length} Modules Selected)`}</span></div>
                            <div className='overflow-y grid-container' style={heightStyle('85%')}>
                                {
                                    inventoryModules?.map(data => (
                                        <div className='d-flex align-items-center mb-3'>
                                            <table>
                                                <td style={{ verticalAlign: 'middle' }}>
                                                    <CSwitch
                                                        className="float-right mr-3"
                                                        shape="pill"
                                                        color="success" size={width <= 500 ? "sm" : "md"}
                                                        defaultChecked={userModules?.includes(data.value) ? true : false}
                                                        onChange={() => onChangeUserData({ user, setUser, value: data, type: 'userModule' })} /></td>

                                                <td style={{ verticalAlign: 'middle' }}><div className='text' style={{ fontWeight: 400 }}>{data.label}</div></td>
                                            </table>
                                        </div>
                                    ))
                                }
                            </div>
                        </div>

                        <div style={{ backgroundColor: '#F9FAFC', height: '38%', overflow: 'hidden' }} className='mb-3 p-3 rounded '>
                            <div style={{ height: 'fit-content' }} className='LatoBold mb-2'>SKU Category Access <span>{`( ${userCategories?.length} Categories Selected)`}</span></div>

                            <div className={` border rounded border-soft-green w-100 bg-white d-flex align-items-center input-height px-2 mb-3`}>
                                <input autoComplete='off' placeholder='Enter Category' className='w-100 plain-input' onChange={(e) => setCategory(e.target.value)} />
                            </div>

                            <div className='overflow-y' style={heightStyle(categoryHeight)}>
                                <div className='grid-container'>
                                    {
                                        categoryList?.filter(data => {
                                            if (data?.label?.toLowerCase().includes(category)) return data
                                        })?.map(data => (
                                            <div className='d-flex align-items-center mb-3'>
                                                <table>
                                                    <td style={{ verticalAlign: 'middle' }}>
                                                        <CSwitch
                                                            className="float-right mr-3"
                                                            shape="pill"
                                                            color="success" size={width <= 500 ? "sm" : "md"}
                                                            defaultChecked={userCategories?.includes(data?.value) ? true : false}
                                                            onChange={() => onChangeUserData({ user, setUser, value: data, type: 'userCategory' })} /></td>

                                                    <td style={{ verticalAlign: 'middle' }}><div className='text' style={{ fontWeight: 400 }}>{data.label}</div></td>
                                                </table>
                                            </div>
                                        ))
                                    }
                                </div>
                            </div>
                        </div>

                        <CButton onClick={() => thereAreRequest > 0 ? false : submitUserList({ user, setUser })} className={`${thereAreRequest > 0 ? 'bg-softGreen text' : 'bg-green text-white'} no-shadow sku-item px-5 w-100`}>{`${thereAreRequest > 0 ? 'Processing...' : 'Update'}`}</CButton>

                    </CModalBody>
                </CModal>
            </CCol>
        </CRow >
    )
}

export default UMEdit