import React from 'react'
import Img from 'assets/icons/sayur-box-logo.svg'
import AdminIcon from 'assets/icons/adminIcon.svg'
import { CTooltip } from '@coreui/react'

const showUserHandler = ({ user, setUser }) => {
    let newUser = { ...user }
    newUser.show = true
    newUser.submitted = false
    setUser(newUser)
}

const UMSummaryCard = ({ data, setUser }) => {
    const image = Img
    const mobile = true
    const userName = data?.email
    const userArea = `${data?.role?.label || ''} - ${data?.warehouse?.code || ''} - ${data?.warehouse?.name || ''}`
    const modules = data?.modules?.length || 0
    const category = data?.categories?.length || 0
    const userRole = data?.role?.value

    const width = document.documentElement.clientWidth
    return (
        <div onKeyDown={(e) => e.keyCode === 13 ? false : false} tabindex="0" className={`w-100 border d-md-flex sku-item um-list-card justify-content-between ${mobile ? 'pl-0 pr-0' : 'pl-2 pr-3'} py-2 mb-3 rounded`}>
            <div className={`d-flex align-items-center w-100 cursor-pointer ${mobile ? 'pl-2 pr-3' : 'pl-0 pr-0'}`} onClick={(false)}>
                <img
                    src={image} alt='sku image'
                    className='w-5 mr-3 border softGreen rounded' />
                <div className='text-left'>
                    <div className={`mr-3 text LatoBold fontSizeTitle d-flex`}>{userName}
                        <CTooltip advancedOptions={{ appendToBody: true, html: true }} content={`<div style='text-align:left'>Only Admin able to give module and SKU category access to other users.</div>`} placement={width <= 500 ? 'left' : 'right'}>
                            <div>
                                <img style={{
                                    opacity: 1,
                                    transition: 'opacity ease-in',
                                    transitionProperty: 'all',
                                    transitionDuration: '0.10s',
                                }} className={`pl-1 ${userRole === 'admin' ? '' : 'd-none'}`} alt='img' src={AdminIcon} />
                            </div>
                        </CTooltip>
                    </div>
                    <div className='text'>{userArea}</div>
                </div>
            </div>
            <hr className={`${mobile ? null : 'd-none'}`} />
            <div className={`d-flex w-100 ${mobile ? 'pr-3' : 'pr-0'} pl-5 pl-md-0 align-items-center`}>
                <div className={`d-flex justify-content-md-end align-items-center w-100 ${mobile ? 'justify-content-end' : null}`}>
                    <div className='mr-1 font-weight-bold emptyTextField'>{`${modules} Modules`}</div>
                    <div className={`mr-3 ml-3 emptyTextField iconU-sideBarDot d-flex align-items-center`} />
                    <div className='mr-3 font-weight-bold emptyTextField'>{`${category} SKU Categories`}</div>
                </div>
                <div onClick={() => showUserHandler({ user: data, setUser })} style={{ fontSize: 20 }} className={`cursor-pointer iconU-tableEdit font-weight-bold green d-flex align-items-center justify-content-center`}></div>
            </div>
        </div>
    )
}

export default UMSummaryCard