import React, { useState, useEffect } from 'react'
import Select from 'react-select'

const Dropdown = ({
    isEmpty,
    title,
    placeholder,
    options,
    selectedValue,
    showTitle,
    width,
    className,
    entryListIdx,
    onChangeDropdown,
    show,
    poListIdx,
    isDisabled,
    style,
    noMarginBottom,
    resetName,
    isMulti,
    hideResetOpt,
    zInfinity
}) => {
    const onChangeHandler = (selected) => {
        onChangeDropdown(selected)
        setIsOpen(false)
    }
    const [isOpen, setIsOpen] = useState()
    useEffect(() => {
        if (selectedValue === 'empty') setIsOpen(true)
    }, [selectedValue, isEmpty])
    const isEmptyStyle = isEmpty ? (styles) => {
        return {
            ...styles,
            border: 'solid #eb5c5c 1px',
            ':hover': { border: 'solid #eb5c5c 1px !important' },
            ':active': { border: 'solid #eb5c5c 1px !important' },

        }
    } : false

    const elem = document?.getElementById(`dropdown${entryListIdx}${poListIdx}`);
    let position = elem?.getBoundingClientRect()

    useEffect(() => {
        position = elem?.getBoundingClientRect()
    }, [isOpen])

    const dimention = document.documentElement.clientWidth
    let defaultvalue = [{ label: `-${resetName ? resetName : title}-`, value: null }]
    if (options) defaultvalue = [...defaultvalue, ...options]
    if (hideResetOpt) defaultvalue = defaultvalue.filter(val => val?.value)
    let menuPlacement = position?.bottom > 600 ? 'top' : 'bottom'
    if (dimention <= 500) {
        menuPlacement = position?.bottom > 400 ? 'top' : 'bottom'
        if (title === 'Categories') menuPlacement = 'top'
    }
    return (
        <div style={style} className={`mr-0 ${style ? null : 'mr-lg-3'} ${noMarginBottom ? null : 'mb-3'} ${width} ${className} ${show ? null : 'd-none'}`}>
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>
            <Select
                inputId={`${title}_${poListIdx}_${entryListIdx}`}
                isMulti={isMulti}
                isSearchable={dimention < 500 ? false : true}
                isDisabled={isDisabled ? isDisabled : false}
                id={`dropdown${entryListIdx}${poListIdx}`}
                value={selectedValue?.value ? selectedValue : (selectedValue?.length ? selectedValue : false)}
                menuIsOpen={isOpen}
                menuPortal
                placeholder={resetName ? resetName : placeholder}
                options={defaultvalue}
                onMenuOpen={() => setIsOpen(true)}
                onMenuClose={() => setIsOpen(false)}
                onChange={onChangeHandler}
                menuPortalTarget={document.body}
                maxMenuHeight={150}
                menuPlacement={`${menuPlacement}`}
                // isClearable={true}
                styles={{
                    menuPortal: base => ({ ...base, zIndex: zInfinity ? 9999999999999999 : 9999 }),
                    control: isEmptyStyle,
                    option: (styles,
                        { isFocused, isSelected }) => {
                        return {
                            ...styles,
                            backgroundColor: isSelected ? 'rgb(242, 250, 246, 0)' : isFocused ? 'rgb(242, 250, 246)' : null,
                            color: isSelected ? 'black' : '#969696',
                            cursor: 'pointer',
                            fontWeight: isSelected ? 'bold' : null,
                            ':active': {
                                backgroundColor: 'rgb(242, 250, 246) !important',
                                color: '#4abe84'
                            },
                            ':hover': {
                                color: !isSelected ? '#4abe84' : null
                            },
                        };
                    },
                }}
                theme={(theme) => ({
                    ...theme,
                    borderRadius: 4,
                })} />
        </div >
    )
}

export default Dropdown