import { useDimension } from 'Page/StockEntry/GeneralServices'
import { useState, useEffect } from 'react'

const onChangeHandler = ({ minLength, setMinLength, maxLength, setMaxLength, i, page, totalPage, setActivePage }) => {
    const { key } = page

    if (key >= 0 && key < totalPage.length) setActivePage(key)
    if (key === maxLength && maxLength < totalPage.length) {
        setActivePage(key)
        setMaxLength(maxLength + (key === (totalPage.length - 1) ? 0 : 2))
        setMinLength(minLength + (key === (totalPage.length - 1) ? 0 : 2))
    }

    if ((key === minLength) && minLength > 0 && key > 0) {
        setActivePage(key)
        setMaxLength(maxLength - 2)
        setMinLength(minLength - 2)
    }
}

const toStartPage = ({ setActivePage, setMaxLength, setMinLength }) => {
    setActivePage(0)
    setMaxLength(2)
    setMinLength(0)
}

const toEndPage = ({ setActivePage, setMaxLength, setMinLength, totalPage }) => {
    setActivePage(totalPage?.length - 1)
    setMaxLength(totalPage.length - 1)
    setMinLength(totalPage.length - 3)
}

export const usePagination = (totalEntries) => {
    const [totalPage, setTotalPage] = useState([])
    const [activePage, setActivePage] = useState(0)
    const [minLength, setMinLength] = useState(0)
    const [maxLength, setMaxLength] = useState(2)
    const { height } = useDimension(250)
    const entriesLength = totalEntries?.length
    useEffect(() => {

        if (entriesLength) {
            const total = Math.ceil(entriesLength / 20)
            let newTotalPage = []
            for (let i = 0; i < total; i++) {
                newTotalPage.push({ key: i })
            }
            setTotalPage(newTotalPage)
        }
    }, [totalEntries]);

    const numberOfPage = entriesLength || 0
    const startPage = (activePage * 20) + 1 || 1
    const endPage = activePage === (totalPage.length - 1) ? entriesLength : ((activePage + 1) * 20)

    return { totalPage, setTotalPage, activePage, setActivePage, minLength, setMinLength, maxLength, setMaxLength, height, onChangeHandler, toStartPage, toEndPage, startPage, endPage, numberOfPage }
}

export const heightStyle = (height, bgColor) => {
    return {
        height,
        minHeight: height,
        maxHeight: height,
        backgroundColor: bgColor
    }
}