import React, { useState, useEffect, useRef } from 'react'
import { useSelector } from 'react-redux'
import filterAscGreen from 'assets/icons/ascOn.svg'
import filterAscGrey from 'assets/icons/ascOff.svg'
import filterCategoryGreen from 'assets/icons/categoryOn.svg'
import filterCategoryGrey from 'assets/icons/categoryOff.svg'
import deleteOn from 'assets/icons/deleteCategoryOn.svg'
import deleteOff from 'assets/icons/deleteCategoryOff.svg'
import checkHover from 'assets/icons/checkHover.svg'
import checkNotHover from 'assets/icons/checkNotHover.svg'
import { useEntryDimention } from 'Page/StockEntry/GeneralServices'
import './index.scss'

const Filter = ({
    isMulti,
    options,
    onChange,
    moduleName
}) => {
    const [show, setShow] = useState(false)
    const { dimention } = useEntryDimention()
    const isMobile = dimention <= 500 ? true : false
    const wrapperRef = useRef(null);
    const leftValue = () => {

        if (dimention >= 1024 && dimention < 1280) return -180
        if (dimention >= 1280 && dimention < 1366) return -80
        if (dimention >= 1366 && dimention < 1396) return -50
        if (dimention >= 1396 && dimention < 1440) return -90
        if (dimention >= 1440 && dimention < 1536) return -70
        if (dimention >= 1536 && dimention < 1600) return -50
        if (dimention >= 1600 && dimention < 1920) return -70
        if (dimention >= 1920 && dimention < 1930) return -50

        // switch (dimention) {
        //     case 1920:
        //         return -50
        //     case 1600:
        //         return -70
        //     case 1536:
        //         return -30
        //     case 1440:
        //         return -70
        //     case 1396:
        //         return -90
        //     case 1366:
        //         return -50
        //     case 1280:
        //         return -80
        //     case 1024:
        //         return -180
        // }
    }

    const topValue = (moduleName, options, isMulti) => {
        switch (dimention) {
            case 1024:
                return -250
            default:
                if (moduleName === 'waste' || moduleName === 'ADD_SKU') return -100
                return -220
        }
    }



    //handle auto adjust position

    const elem = document?.getElementById(`c-filter`);
    let rectPosition = elem?.getBoundingClientRect()

    useEffect(() => {
        rectPosition = elem?.getBoundingClientRect()
    }, [show])

    const top = rectPosition?.top + (isMobile ? -70 : topValue(moduleName, options, isMulti))

    //handle klick outside

    function useOutside() {
        useEffect(() => {
            function handleClickOutside(event) {
                if (show && wrapperRef.current && !wrapperRef.current.contains(event.target)) {
                    setShow(!show)
                }
            }

            // Bind the event listener
            document.addEventListener("mousedown", handleClickOutside);
            return () => {
                // Unbind the event listener on clean up
                document.removeEventListener("mousedown", handleClickOutside);
            };
        }, [wrapperRef, show]);
    }

    useOutside();

    const maxHeight = show ? 280 : 0
    const opacity = show ? 1 : 0

    const left = isMobile ? '1rem' : leftValue()
    const right = isMobile ? '1rem' : false
    const position = isMobile ? 'fixed' : 'absolute'
    const minWidth = isMobile ? '100% !important' : 250

    // data variable
    const [search, setSearch] = useState('')
    const [selected, setSelected] = useState(isMulti ? [] : {})
    const [active, setActive] = useState(false)
    const handleSelected = ({ options, item, setSelected, reset }) => {
        let newOptions = options?.map(data => data)
        newOptions = newOptions.map(data => {
            if (item === data.value) data.selected = !data.selected
            if (reset) data.selected = false
            return data
        })
        newOptions = newOptions.filter(data => data.selected)
        setSelected(newOptions)
        if (typeof (onChange) === 'function') onChange(newOptions)
    }

    const handleSingleSelected = (selected) => {
        setSelected(selected)
        if (typeof (onChange) === 'function') onChange(selected)
        setShow(false)
    }
    const filterBySelected = ((a, b) => {
        if (a.selected && !b.selected) return -1
        else if (!a.selected && b.selected) return 1
        else return 0
    })

    const targetRepack = useSelector(state => state.targetRepack)
    const stockMovementTargetRepack = useSelector(state => state.stockMovementTargetRepack)
    const thereIsTargetRepack = (targetRepack && moduleName === 'repack') || (stockMovementTargetRepack && moduleName === 'stockMovement') ? true : false


    let sortOptions = [
        { value: 'asc', label: 'Ascending' },
        { value: 'desc', label: 'Descending' },
        { target: true, value: 'tasc', label: 'Most Completion Target' },
        { target: true, value: 'tdesc', label: 'Least Completion Target' },
    ]

    return (
        <div className='position-sticky'>
            <div>

                <img className='cursor-pointer' id='c-filter' src={isMulti ? (selected?.length ? filterCategoryGreen : filterCategoryGrey) : (selected?.label ? filterAscGreen : filterAscGrey)} onClick={() => setShow(true)} />

            </div>
            <div ref={wrapperRef} className='text mt-3 rounded shadow py-2'
                style={{
                    zIndex: 1040,
                    transition: 'max-height ease-in',
                    transitionProperty: 'all',
                    transitionDuration: '0.15s',
                    maxHeight,
                    opacity,
                    minWidth,
                    backgroundColor: 'white',
                    top,
                    position,
                    left,
                    right,
                    overflow: 'hidden',
                    // display: show ? false : 'none'
                }}>

                <div style={{ zIndex: 1040, maxHeight: 230, overflow: 'scroll' }} className={`hide-scrollbar ${isMulti ? 'd-none' : null}`}>
                    {
                        !isMulti && sortOptions?.map(data => {
                            const currentItem = data.label === active ? true : false
                            const itemOpacity = currentItem ? 1 : 0
                            const itemTextStyle = currentItem ? (active === data.label ? 'green' : 'text') : 'emptyTextField'

                            return (
                                <div
                                    onMouseEnter={() => setActive(data.label)}
                                    onMouseLeave={() => setActive(null)}
                                    onClick={() => handleSingleSelected(data)}
                                    style={{ minHeight: 40, backgroundColor: 'white' }}
                                    className={`${data.target ? (thereIsTargetRepack ? null : 'd-none') : null} filter-items rounded cursor-pointer w-100 d-flex justify-content-between align-items-center px-3 ${data?.label === selected?.label ? 'LatoBold text' : false} ${itemTextStyle}`}>
                                    {data.label}
                                </div>
                            )
                        })
                    }
                </div>

                <div className={`px-2 ${isMulti ? null : 'd-none'}`}>
                    <div className='border rounded w-100 d-flex justify-content-between align-items-center pl-3 pr-2' style={{ height: 40, }}>
                        <input id='filter-input' value={search} placeholder='Search Category' onChange={e => setSearch(e.target.value)} type='text' className='plain-input m-0 p-0' style={{
                            color: '#969696'
                            , fontSize: '1rem'
                        }} />
                        <div className='green'>
                            <img
                                src={selected?.length ? deleteOn : deleteOff}
                                alt="delete icon"
                                className="cursor-pointer"
                                onClick={(e) => handleSelected({ options, setSelected, reset: true })}
                            />
                        </div>
                    </div>
                </div>

                <div style={{ zIndex: 1040, maxHeight: 230, overflow: 'scroll' }} className='hide-scrollbar'>
                    {
                        isMulti && options?.filter(name => {
                            if (search?.length) {
                                if (name.label.toLowerCase().includes(search.toLowerCase())) return name
                            }
                            else return name
                        }).sort(function (a, b) { return filterBySelected(a, b) }).map((data, idx) => {
                            const currentItem = selected.filter(item => item?.value === data?.value)?.length ? true : false
                            const itemOpacity = currentItem ? 1 : 0
                            const itemTextStyle = isMulti ? (currentItem ? (active === idx ? 'green' : 'text') : 'emptyTextField') : (selected ? 'text' : 'emptyTextField')

                            return (
                                <div
                                    onMouseEnter={() => setActive(idx)}
                                    onMouseLeave={() => setActive(null)}
                                    onClick={() => handleSelected({ options, item: data.value, setSelected })}
                                    style={{ minHeight: 40, backgroundColor: 'white' }}
                                    className={`filter-items rounded cursor-pointer w-100 d-flex justify-content-between align-items-center ${itemTextStyle}`}>
                                    <table style={{ tableLayout: 'fixed', width: '100%' }}>
                                        <tr>
                                            <td align={'center'} style={{ width: '20%' }} ><img style={{ opacity: itemOpacity }} src={active === idx && currentItem ? checkHover : checkNotHover} /></td>
                                            <td style={{ width: '80%' }} >{data.label}</td>
                                        </tr>
                                    </table>
                                </div>
                            )
                        })
                    }
                </div>
            </div>

        </div >
    )
}

export default Filter