import React from 'react'
import UMSummaryCard from 'Page/ConfigModules/UserManagement/UMSummaryCard'
import { usePagination } from './services'
import loading from "assets/icons/loading/LOADING-MLS-GRAY.gif";


const Pagination = ({ userList, setUser, thereAreRequest }) => {
    const { totalPage, activePage, setActivePage, minLength, setMinLength, maxLength, setMaxLength, height, onChangeHandler, toStartPage, toEndPage, startPage, endPage, numberOfPage } = usePagination(userList)

    if (thereAreRequest > 0 && userList?.length === 0) {
        return <div className="w-100 text-center emptyTextField d-flex flex-column align-items-center justify-content-center" style={{ minHeight: height, maxHeight: height }}>
            <img className="loading" src={loading} alt="loading" />
        </div>
    }
    else if (thereAreRequest === 0 && userList?.length === 0) {
        return <div className="w-100 text-center emptyTextField d-flex flex-column align-items-center justify-content-center" style={{ minHeight: height, maxHeight: height }}>
            Search User
        </div>
    }
    else return (
        <div className='w-100'>
            <div
                className={`px-3 d-flex align-items-center scroll-sku flex-column w-100 text-center`} style={{ minHeight: height, maxHeight: height }}>
                {
                    userList?.filter((data, idx) => idx >= (activePage * 20) && idx < (20 * (activePage + 1)))?.map(data => <UMSummaryCard setUser={setUser} data={data} />)
                }
            </div>
            <div className={`d-md-flex border-top py-3 px-4 justify-content-between w-100 ${userList?.length ? null : 'd-none'}`}>
                <div className='w-100'></div>
                <div className='d-flex w-100 justify-content-center mb-3 mb-md-0'>
                    <div
                        onClick={() => toStartPage({ setActivePage, setMaxLength, setMinLength })}
                        className={` ${'emptyTextField'} ${''} h6 fontSizeSmallTitle d-flex align-items-center p-pgn-btn rounded mr-3 iconU-paginationPrevMost`} />
                    <div
                        onClick={() => onChangeHandler({ minLength, setMinLength, maxLength, setMaxLength, page: { key: activePage - 1 }, totalPage, setActivePage })} className={` ${'emptyTextField'} ${''} h6 fontSizeSmallTitle d-flex align-items-center p-pgn-btn rounded mr-3 iconU-paginationPrev`} />
                    {
                        totalPage?.filter((data, idx) => idx >= minLength && idx <= maxLength)?.map((page, i) => {
                            return <div onClick={() => onChangeHandler({ minLength, setMinLength, maxLength, setMaxLength, i, page, totalPage, setActivePage })} style={{ width: 25, height: 25, transitionDuration: '0.2s', transition: 'background-color linear', transitionProperty: 'all', backgroundColor: 'transparent' }} className={` ${activePage === page.key ? 'bg-green text-white' : 'emptyTextField'} h6 d-flex align-items-center justify-content-center p-1 cursor-pointer rounded mr-3 p-pgn-btn`}>{page.key + 1}</div>
                        })
                    }
                    <div onClick={() => onChangeHandler({ minLength, setMinLength, maxLength, setMaxLength, page: { key: activePage + 1 }, totalPage, setActivePage })} className={` ${'emptyTextField'} ${''} h6 fontSizeSmallTitle d-flex align-items-center d-flex align-items-center p-pgn-btn rounded mr-3 iconU-paginationNext`} />
                    <div onClick={() => toEndPage({ setActivePage, setMaxLength, setMinLength, totalPage })} className={` ${'emptyTextField'} ${''} h6 fontSizeSmallTitle d-flex align-items-center d-flex align-items-center p-pgn-btn rounded mr-3 iconU-paginationNextMost`} />
                </div>
                <div className='d-flex w-100 text justify-content-center align-items-center justify-content-lg-end'>
                    <div>{`Showing `}</div>
                    <div className='LatoBold ml-1 mr-1'>{` ${startPage} to ${endPage} of ${numberOfPage} `}</div>
                    <div>{` entries`}</div>
                </div>
            </div>

        </div>
    )
}

export default Pagination