import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { filterToggle } from './services'
import barcodeActive from 'assets/icons/barcodeActive.svg'
import barcodeInActive from 'assets/icons/barcodeInactive.svg'
import Filter from 'Component/Filter'

const Search = ({
    placeholder = '',
    textSearch = '', //text search
    searchHandler = null, //fungsi ketika tombol search di klik
    barcodeScanner,
    filter = '', //isi dari filter
    width = 'w-100',
    className = null,
    filterDropdown = null,
    value,
    showScanner,
    areaId,
    setAreaId,
    areaDestination,
    setAreaDestination,
    moduleName,
    setSearch,
    category,
    setCategory,
    setSortBy,
    hideFilter,
    useScanner
}) => {
    const [filterShow = false, setFilterShow] = useState()
    let filterText = ''
    let filterIcon = ''
    if (filterShow) {
        filterText = "Show Filter"
        filterIcon = ''

    } else {
        filterText = `Hide Filter`
        filterIcon = <i className="p-2 rounded-circle bg-background iconU-entryTrash"></i>
    }
    //params
    const [desc, setDesc] = useState('')
    const categoryList = useSelector(state => state.categoryList)

    const search = (e) => {
        if (e.key === 'Enter') searchHandler(desc)
    }

    const scannerChecker = ({ areaId, areaDestination, setAreaId, setAreaDestination, barcodeScanner, moduleName }) => {
        if (moduleName !== 'stockMovement') {
            if (areaId?.value) {
                barcodeScanner(true)
                return
            }
            if (typeof (setAreaId) === 'function') setAreaId('empty')
            return
        }
        if (moduleName === 'stockMovement') {
            if (!areaId?.value) {
                setAreaId('empty')
                return
            }
            if (!areaDestination?.value) {
                setAreaDestination('empty')
                return
            }
            if (areaId?.value === areaDestination?.value) {
                setAreaDestination('empty')
                return
            }
            if (typeof (barcodeScanner) === 'function') barcodeScanner(true)
        }
    }
    return (
        <div className="search-bar w-100">
            <div className="bg-white d-flex justify-content-between border-all radius-all pl-3 pr-1 py-1 search-m w-100">
                <div className="d-flex align-items-center sub-search-m w-100">
                    <i className="iconU-searchBarSearch fontSizeTitle emptyTextField border-right pr-3"></i>
                    <div className="px-lg-3 px-0 input-search d-flex align-items-center">
                        <input
                            type="text"
                            className="borber-none plain-input"
                            placeholder={placeholder}
                            onChange={(e) => {
                                setDesc(e.target.value)
                                if (typeof (setSearch) === 'function') setSearch(e.target.value)
                                // value(e.target.value)
                            }}
                            onKeyPress={(e) => search(e)}
                        />
                    </div>
                </div>
                <div className={`w-100 d-flex align-items-center mb-md-0 mb-3 ${hideFilter ? 'd-none' : null}`}>
                    <div className='mr-2 w-100 d-flex justify-content-end'>
                        {/* <Dropdown
                            isMulti={true}
                            noMarginBottom={true}
                            onChangeDropdown={(selected) => {
                                if (typeof (setCategory) === 'function') setCategory(selected)
                            }}
                            selectedValue={category}
                            options={categoryList}
                            className='w-30'
                            placeholder='Select Category'
                            title='Categories'
                            show /> */}

                    </div>
                    <div className='mr-md-2'>
                        <Filter
                            moduleName={moduleName}
                            selectedValue={category}
                            options={categoryList}
                            onChange={(selected) => setSortBy(selected)}
                        />
                    </div>
                    <div className=''>
                        <Filter
                            moduleName={moduleName}
                            isMulti={true}
                            selectedValue={category}
                            options={categoryList}
                            onChange={(selected) => setCategory(selected)}
                        />
                    </div>
                    <div className='mr-0 mr-md-2'>
                        <img onClick={() => scannerChecker({ areaId, areaDestination, setAreaId, setAreaDestination, barcodeScanner, moduleName })} className={`ml-2 pointer ${useScanner ? null : 'd-none'}`} src={showScanner ? barcodeActive : barcodeInActive} />
                    </div>
                </div>
                <div className="text d-flex align-items-center btn-c-m" style={{ width: 'fit-content' }}>
                    {filter ?
                        <div className="text pointer d-flex sub-btn-m" onClick={() => filterToggle({ filterShow, setFilterShow })}><div className="filter-text">{filterText}</div> <span className="px-3"><i className="fontSizeTitle iconU-searchFilter pr-3"></i>{filterIcon}</span></div> : null}
                    <button className="btn px-5 py-2 text-white bg-green LatoBold no-shadow" style={{ height: "fit-content !important" }} onClick={() => searchHandler(desc)} >Search</button>
                </div>
            </div>
            <div>{(!filterShow ? <div className='w-100'> {filterDropdown} </div> : null)}</div>
        </div>
    )
}

export default Search